:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;
  
}
body{
  background-color: #222222;
  -khtml-user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 95vw;
  margin: auto;
}
#content img{
  border-radius: 1vw 0;
}
#Header{
  font-size: var(--Nav-font-size);
  box-sizing: border-box;
  margin:0 auto;
  width: 94.5vw;
  min-height: 55px;
  height: 5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: 10px;}
#NavHeader-ul a{
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
}
#divImg{
  min-width: 40px;
  min-height: 40px;
  width: 3.1vw;
  height: 3.1vw;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}

#Search{
  background-color: #1a1a1a;
  border-radius: 5px;
  position: relative;
  display: block;
  padding: 5px 12px;
  text-decoration: none;
  padding-left: 36px;
}
#Search::after{
  position: absolute;
  top: 4px;
  left: 6px;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  min-height: 35px;
  min-width: 35px;
  width: 2.65vw;
  height: 2.65vw;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }


footer{
  position: absolute;
  left: 0;
  margin-top: 4.5vw;
  width: 100%;
  height: 9vw;
  min-height: 150px;
  background-color: #141414;
}





#Error {
  width: 94vw;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 1vw 0 1vw 0;
  width: 100%;
  user-select: none;
  min-height: 220px;
  height: 26vw;

}

#MainSliderContainer{
  min-height: 190px;
  height: 24.76vw;
  transition: .5s;
  position: relative;
  display: flex;
}
.MainSliderChild{
  box-sizing: border-box;
  background-color: #181818;
  min-width: 100%;
  padding: 1%;
}
.MainSliderChild a{
  outline: none;
  text-decoration: none;
  height: 100%;
  cursor: default;
  display: flex;
  align-items: center;
}

.MainSliderChild img{
  background-color: #141414;
  width: 21vw;
  height: 100%;
}
.MainSliderChild div{
  overflow: hidden;
  pointer-events: none;
  min-height: 175px;
  height: 24.76vw;
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
}
.MainSliderChild div h2, .MainSliderChild div p{
  margin: 0px;
  padding: 0px;
}



#HotTitles{
  background-color: #181818;
  display: flex;
  width: 100%;
}
#HotTitlesHeader{
  background-color: #18181896;
  border-radius: 1vw 1vw 0 0 ;
  font-size: 1.1em;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  min-height: 55px;
  height: 4.5vw;
  width: 100%;

}
#HotTitlesTop{
  position: relative;
  overflow: hidden;
  width: 40%;
  height: 16.23vw;
  min-height: 180px;
}
#HotTitlesSlider{
  border-left: 1px solid rgb(105, 105, 105);
  background-color: #181818;
  overflow: hidden;
  width: 60%;
  height: 16.23vw;
  min-height: 180px;
}
#HotTitlesSliderContainer{
  transition-timing-function: linear;
  height: 100%;
  display: flex;
  align-items: center;
  width:100%;
}
#HotTitlesTopContainer{
  transition: .3s;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#HotTitlesTopContainer a{
  display: block;
}
.HotTitlesTopChild img{
  transition: .3s;
  display: block;
  min-width: 90px;
  min-height: 130px;
  width: 8.304vw;
  height: 12.1vw;
  background-color: #141414;
}
.HotTitlesTopChildFocus a:focus, .HotTitlesTopChild a:focus{
  outline: 2px solid rgb(223, 52, 0);
}
.HotTitlesTopChildFocus img{
  transition: .3s;
  display: block;
  min-width: 120px;
  min-height: 165px;
  width: 10.4917vw;
  height: 14.35vw;
  background-color: #141414;
}
.HotTitlesTopDescription{
  text-decoration: none;
  box-sizing: border-box;
  background-color: #141414;
  transition: .5s;
  height: 100%;
  padding: 25px;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
.HotTitlesSliderChild{
  text-align: center;

  flex: 0 0 auto;
  width: 11vw;
  min-width: 130px;
}

.HotTitlesSliderChild img{
  width: 8.304vw;
  height: 12.1vw;
  min-width: 90px;
  min-height: 130px;
  background-color: #141414;
}

#RankingTitlesContainer{
  box-sizing: border-box;
  margin-top: 2vw;
  overflow-x: auto;
  display: flex;
  justify-content: space-between;
}
.RankingTitlesChild{
  flex: 0 0 auto;
  min-width: 330px;
  width: 33%;
}
.RankingTitlesChild > div{
  padding: 1vw;
  background-color: #181818;
  min-height: 145px;
  height: 11vw;
  font-size: .835em;
  display: flex;
  justify-content: space-between;
}
.RankingTitlesChild > h2{
  background-color: #181818;
  border-radius: 1vw 1vw 0 0 ;
  padding-left: 2vw;
  min-height: 60px;
  display: flex;
  align-items: center;
}
.RankingTitlesChild a {
  text-decoration: none;
}

.RankingTitlesChild>div>div{
  width: 22.2vw;
  min-width: 210px;
  box-sizing: border-box;

}
.RankingTitlesChild>div>div>a h2{
  font-weight: 500;
}
.RankingTitlesChild img{
  min-width: 85px;
  min-height: 120px;
  width:6.422vw;
  height:9.06vw
}

.RankingTitlesInfo{
  width: 100%;
  height: 7vw;
  min-height: 90px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.RankingTitlesInfo > div:first-child{
  overflow-x: hidden;
}

.RankingTitlesInfo > div:nth-child(2) > div:first-child{
  color: #727272;
}
.RankingTitlesInfo>div:nth-child(2) >div:last-child{
  color: #9b4b00;
}
.RankingTitlesSliderGenres{
  display: flex;
}
.RankingTitlesSliderGenres > div{
  flex: 0 0 auto;
  width: 8.5vw;
  min-width: 90px;
}
.RankingTitlesSliderGenres a{
  text-decoration: none;
  display: block;
  padding: 1px;
  border-radius: 1em;
  min-width: 70px;
  width: 7.8vw;
  text-align: center;
  background-color: #9b4a14a8;
}
#LastUpdates{
  margin-top: 1vw;
}
#LastUpdates > h2{
  border-radius: 1vw 1vw 0 0;
  padding: .5vw;
  background-color: #33333360;
  text-align: center;
  height: 3vw;
  min-height: 40px;
}
#LastUpdatesContainer{
  background-color: #191919b6;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.LastUpdatesChild{
  background-color: #181818;
  border-radius: 2vw;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 160px;
  min-width: 350px;
  width: 30vw;
  height: 12vw;
  margin: .5vw;
}
.LastUpdatesChild a{
  min-width: 340px;
  width: 29vw;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
}
.LastUpdatesChild:hover{
  background-color: #131313;
}
.LastUpdatesChild img{
  min-width: 100px;
  min-height: 140px;
  width: 7.56vw;
  height: 10.58vw
}
.LastUpdatesChild h2{
  font-weight: 500;
  font-size: 1.25em;
}
.LastUpdatesChild p {
  color: #b1b1b1;
  overflow: hidden;
  font-size: .94em;
  min-width: 230px;
  width: 21vw;
  min-height: 85px;
  height: 8.5vw;
}


#TitleHeader .RankingTitlesSliderGenres{
  font-size: .95em;
}
#TitleHeader .RankingTitlesSliderGenres div{
  min-width: 100px;
}
#TitleHeader .RankingTitlesSliderGenres a{
  min-width: 90px;
}
#TitleHeader{
  background: #1a1a1a;
  width: 100%;
  height: 20vw;
  min-height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#TitleHeader > div img{

  width: 12vw;
  min-width: 140px;
}
#TitleHeader span{
  font-weight: 500;
  color: #858585a4;
  font-size: .93em;
}
#TitleHeaderContainer > div:last-child{
  margin-left: 2vw;
}
#TitleHeaderContainer>div:last-child h3{
  font-size: 1.5em;
}
#TitleHeaderContainer>div:last-child h4{
  margin-left: 1vw;
}
#TitleHeaderContainer{
  overflow: hidden;
  display: flex;
  width: 95%;
}
#TitleHeaderContainer>div:last-child{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#TitleHeaderContainer>div:last-child>div:first-child{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 7vw;
  min-height: 115px;
}
#TitleHeaderContainer>div:last-child>div:last-child{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 15vw;
  min-width: 190px;
}

#TitleHeaderContainer button{
  cursor: pointer;
  border-radius: .3vw;
  background-color: #9b4b00;
  padding: 6px 18px;
  border: none;
  margin-right: 10px;
}

#TitleHeaderContainer button:hover{
  background-color: #884200;
}
#TitleHeaderContainer button:last-child{
  background-color: #773a00;
}
#TitleBody{
  margin-top: 2vw;
}
#TitleBodyContainer{
  padding: 1vw;
  background-color: #1a1a1a;
  box-sizing: border-box;
}
#TitleBodyHeader{
  border-radius: 1vw 1vw 0 0;
  width: 100%;
  background-color: #272727;
}
#TitleBodyHeader button{
  background-color: #272727;
  font-size: .96em;
  cursor: pointer;
  padding: .4vw .8vw;
  border: none;

}
#TitleBodyHeader button:first-child{
    border-radius: 1vw 0 0 0;
}
#TitleBodyHeader button:focus{
  background-color: #222222;
  outline: none;
}
#TitleBodyHeader button:hover{
  background-color: #202020;
}
#TitleBodyHeader button#focus{
  background-color: #1a1a1a;
}
.TitleBodyContainerChild{
  display: none;
}
#TitleBodyContainer > div.focus{
  display: block;
}

.TitleBodyContainerChild > h4{
  font-size: 1.3em;
  height: 4.3vw;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
#TitleBodyContainerSlider{
  overflow: hidden;
}
#TitleBodyContainerSliderContainer{
  display: flex;
}
#TitleBodyContainerSliderContainer > div{
  flex: 0 0 auto;
  width: 32vw;
  height: 15.1vw;
  min-width: 290px;
  min-height: 160px;
}
#TitleBodyContainerSliderContainer>div>a{
  background-color: #2c2c2c;
  display: flex;
  text-decoration: none;
  width: 30vw;
  height: 14vw;
  min-width: 275px;
  min-height: 150px;
  padding: .6vw;
  justify-content: space-between;
}
#TitleBodyContainerSliderContainer>div>a img{
  flex: 0 0 auto;
  display: block;
  background-color: #141414;
  width: 10.5vw;
  min-width: 100px;
  height: 100%;
}
#TitleBodyContainerSliderContainer>div>a>div{
  width: 18.5vw;
  min-width: 165px;
  overflow: hidden;
}
#TitleBodyContainerSliderContainer>div>a>div h5{
  font-size: 1.1em;
}
#TitleBodyContainerSliderContainer>div>a>div p {
  font-size: .89em;
  color: #cacaca;
}
#Chapters > div{
  margin-top: 1vw;
}
.star{
  display: inline-block;
  width: 20px;
  margin-right: 5px;
  height: 20px;
  background-image: url("../public//imgInterface/star.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.score{
  color: #AF5500;
  display: flex;
  align-items: center;
}
#Comments div:first-child{
  position: relative;
}
#comment {
  overflow-y: hidden;
  box-sizing: border-box;
  background: #222222;
  resize: none;
  border: none;
  padding: .5vw;
  padding-right: 4vw;
  width: 100%;
}

#Comments> div:last-child p{
  word-wrap: break-word;
  flex: 0 0 auto;
  width: inherit;
}
#Comments> div:last-child img {
  margin-right: 1.3vw;
  float: left;
  width: 2.5vw;
  height: 2.5vw;
  min-height: 27px;
  min-width: 27px;
  background-color: #252525;
  border-radius: 100%;
  flex: 0 0 auto;
}
#Comments>div:last-child>div>div{
  width: 100%;
}
#Comments> div:last-child > div{
  margin: 2vw 0;
  display: flex;
}
#Comments>div:last-child button{
  margin-top: .54vw;
  border-radius: .4vw;
  cursor: pointer;
  padding: .3vw;
  background-color: #272727;
  border: none;
}
#Comments>div:last-child button:hover{
  background-color: #222222;
}
.tx-areaContainer{
  box-sizing: border-box;
  position: absolute;
  width: 40px;
  height: 95%;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
}
.tx-areaContainer > div{
  border-radius: .5vw;
  padding: .3vw;
}
.tx-areaContainer>div:hover{
  background: #88420059;
}
.tx-area{
  cursor: pointer;
  display: block;
  width: 2vw;
  height: 2vw;
  min-width: 25px;
  min-height: 25px;
  background: url("../public//imgInterface/paper-plane.png");
  background-repeat:no-repeat;
  background-size: contain;
}
.tx-answer{
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #b35600;
  width: 2vw;
  height: 2vw;
  min-width: 25px;
  min-height: 25px;
}
#Chapters h5{
  height: 4vw;
}
#Chapters button{
  padding: .4vw;
  cursor: pointer;
  border: none;
  background: transparent;
}
#Chapters button:hover{
  background-color: #222222;
}
#Chapters a{
  text-decoration: none;
  padding: .5vw;
  display: block;
  width: inherit;
}
#Chapters a:hover{
  background-color: #222222;
}
@media screen and (max-width:510px) {
  #TitleHeaderContainer{
    display: block;
  }
  #TitleHeaderContainer >div:first-child{
    text-align: center;
  }
  #TitleHeaderContainer>div:first-child img{
    width: 250px;
  }
  #TitleHeader{
    height: 620px;
  }
  #Dots {
    margin-top: 5px;
  }
  #HotTitlesHeader h2{
    min-height: 50px;
  }
  #TitleHeaderContainer>div:last-child{
    background-color: #131313;
    border-radius: 2vw;
    margin: 0;
    height: 200px;
    padding: 15px;
  }
}



@media screen and (max-width:700px) {
  .MainSliderChild img{
    width: 100px;
    height: 135px;
  }
  #MainSlider {
    font-size: .8em;
  }
  #HotTitles {
    display: block;
  }
  #HotTitlesTop{
    min-width: 100%;
  }
  #HotTitlesSlider{
    min-width: 100%;
    border-left: none;
    border-top: 1px solid rgb(105, 105, 105);
  }
    #HotTitlesHeader{
      font-size: 1.1em;
    }
}
@media screen and (max-width: 770px) {
  #TitleHeader .RankingTitlesSliderGenres{
    overflow-x: auto;
    min-height: 40px;
  }
  #HotTitlesSliderContainer{
    overflow-x: scroll;
    
  }
  #TitleBodyContainerSlider{
    overflow-x: auto;
  }
  #MainSlider {
    font-size: .85em;
  }
  
  .HotTitlesTopDescription {
    font-size: .85em;
  }
  .RankingTitlesChild{
    font-size: .9em;
  }
  .RankingTitlesInfo>div:first-child{
    overflow-x: auto;
    min-height: 40px;
  }
  #TitleBody{
  margin-top: 20px;
}
  .LastUpdatesChild{
    width: 100%;
  }
}


@media screen and (max-width: 1400px) {
  .HotTitlesTopDescription {
    font-size: 1.05em;
  }
}
@media screen and (max-width: 1200px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
  .RankingTitlesChild{
    padding-right: 8px;
  }
  #TitleBodyHeader button{
  padding: 5px 10px;
  }
  #TitleBodyContainer{
    padding: 15px;
  }
  #Chapters > div{
    margin-top: 30px;
  }
  #comment {
    padding-right: 40px;
  }
  #HotTitlesHeader,.tx-areaContainer>div,#LastUpdates>h2,.RankingTitlesChild>h2 {
    border-radius: 15px 15px 0 0;
  }
  #content img{
    border-radius: 10px 0;
  }
  #LastUpdates,#RankingTitlesContainer{
    margin-top: 45px;
  }
  .RankingTitlesChild > div{
    padding: 13px;
    box-sizing: border-box;
  }
  .LastUpdatesChild{
    margin: 8px;
  }
}
@media screen and (max-width: 992px) {
  .HotTitlesTopDescription {
    font-size: .98em;
  }
}

@media screen and (max-width: 576px) {
  .HotTitlesTopDescription{
    font-size: .8em;
  }
}


.pointEvent{
  pointer-events: none;
}





